html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  background: #f7f8fa;
  color: #222;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-height: 100vh;
  background: linear-gradient(180deg, #f7f8fa 60%, #e9ebf0 100%);
  letter-spacing: 0.01em;
}

* {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  margin: 0 0 0.5em 0;
}

button, input, textarea, select {
  font-family: inherit;
  font-size: 1rem;
}

::-webkit-scrollbar {
  width: 8px;
  background: #f3f3f3;
}
::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 8px;
}
