.chatgpt-input-bar {
  background: linear-gradient(180deg, #f7f8fa 80%, #e9ebf0 100%);
  border-top: 1px solid #e3e6ec;
  padding: 24px 0 16px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  z-index: 20;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 -2px 16px 0 rgba(0,0,0,0.04);
}

.chatgpt-input-box {
  display: flex;
  align-items: flex-end;
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.06);
  border: 1px solid #e3e6ec;
  padding: 8px 16px;
  width: 100%;
  max-width: 700px;
  gap: 8px;
}

.chatgpt-textarea {
  flex: 1;
  border: none;
  background: transparent;
  font-size: 1.15rem;
  padding: 10px 0;
  outline: none;
  resize: none;
  min-height: 32px;
  max-height: 160px;
  line-height: 1.5;
  color: #222;
  font-family: inherit;
}

.chatgpt-send-btn {
  background: #111;
  color: #fff;
  border: none;
  border-radius: 12px;
  font-size: 1.2rem;
  padding: 10px 14px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background 0.2s, box-shadow 0.2s;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}
.chatgpt-send-btn:hover {
  background: #222;
}

.chatgpt-input-hint {
  margin-top: 8px;
  color: #888;
  font-size: 0.95rem;
  text-align: center;
  user-select: none;
}

.chatgpt-upload-btn {
  background: none;
  border: none;
  color: #888;
  margin-right: 8px;
  padding: 6px 6px 6px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 1.1rem;
  border-radius: 8px;
  transition: background 0.2s, color 0.2s;
}
.chatgpt-upload-btn:hover:not(:disabled) {
  background: #f3f3f3;
  color: #111;
}
.chatgpt-upload-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.chatgpt-input-file-info {
  margin-top: 6px;
  color: #555;
  font-size: 0.98rem;
  background: #f7f7fa;
  border-radius: 8px;
  padding: 4px 12px;
  display: inline-block;
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
} 